import React from 'react'
import Editor from 'react-code-live'
import Header from './Header'
import styled from 'styled-components'
import styles from './styles.module.css'

const App = () => {
  
  /*   function getJsCode(js) {
    console.log("JS LOG",js.substr(0, 10))
  }

  function getCssCode(css) {
    console.log("CSS LOG",css.substr(0, 10))
  } */

  return (
    <div>
      <Header />
      {data.map((el) => (
        <Editor
          key={el.id}
          className={styles.container}
          initialJs={el.js}
          initialCss={el.css}
          textAreaClassName={styles.custom}
          scope={{ styled: el.scope?.styled && styled }}
        />
      ))}
    </div>
  )
}

export default App

const initialCss = `
.__custom__live__react__ {
width:150px;
height:150px;
background:tomato;
color:#FFF;
display:flex;
border-radius: 50%;
justify-content:center;
align-items:center;
padding:5px;
}

.__custom__live__react__ > h5 {
text-align: center;
}
`

const initialCss2 = `
.__custom__live__react__ {
width:150px;
height:150px;
background:lightgreen;
color:#FFF;
display:flex;
border-radius: 50%;
justify-content:center;
align-items:center;
padding:5px;
}

.__custom__live__react__ > h5 {
text-align: center;
}
`

const functionExample = `function Test () {
  return (
    <div className="__custom__live__react__">
      <h5>Functional Component</h5>
    </div>
    )
}`

const arrowFunctionExample = `const Test = () => {
  return (
    <div className="__custom__live__react__">
      <h5>Arrow Function Component!</h5>
    </div>
    )
}`

const classExample = `class Test extends Component {
  render(){
    return (
      <div className="__custom__live__react__">
        <h5>Class Component!</h5>
      </div>
      )
  }
}`

const styledExample = `const WithStyled = () => {
return (
  <StyledDiv>
    <h5>Styled Components</h5>
  </StyledDiv>
)
}

const StyledDiv = styled.div\`
width:150px;
height:150px;
background:tomato;
color:#FFF;
display:flex;
border-radius: 50%;
justify-content:center;
align-items:center;
padding:5px;
& > h5 {
  text-align: center;
}
\`
`

const data = [
  {
    id: 1,
    js: functionExample,
    css: initialCss
  },
  {
    id: 2,
    js: arrowFunctionExample,
    css: initialCss2
  },
  {
    id: 3,
    js: classExample,
    css: initialCss
  },
  {
    id: 4,
    js: styledExample,
    css: initialCss,
    scope: {
      styled: true
    }
  },
  { id: 5 }
]

